const initialState = {
  isLoading: false,
};

const fetchDataReducer = (state = initialState, action) => {
  switch (action.type) {
    case 'START_FETCH_DATA':
      return {
        ...state,
        isLoading: true,
      };
    case 'STOP_FETCH_DATA':
      return {
        ...state,
        isLoading: false,
      };

    default:
      return {
        ...state,
        isLoading: state ? state.isLoading : false,
      };
  }
};

export default fetchDataReducer;
