import React from 'react';
import { Link } from 'react-router';
import Container from 'react-bootstrap/Container';
import Button from 'react-bootstrap/Button';
import PropTypes from 'prop-types';
import html2canvas from 'html2canvas';
import jsPDF from 'jspdf';
import { Table } from 'react-bootstrap';
import AlertMessage from '../public/AlertMessage';

class OrderDetails extends React.Component {
  constructor(props) {
    super(props);

    this.printDocument = this.printDocument.bind(this);

    this.state = {
      pdfName: props.location.state !== undefined ? props.location.state.id : '',
    };
  }

  printDocument() {
    // window.html2canvas = html2canvas;
    const input = document.getElementById('divToPrint');
    html2canvas(input).then((canvas) => {
      const imgData = canvas.toDataURL('image/png', 1);
      // eslint-disable-next-line
      const pdf = new jsPDF();
      pdf.addImage(imgData, 'JPEG', 0, 0, 210, 0);
      // pdf.output('dataurlnewwindow');
      pdf.save(`Invoice Order ID ${this.state.pdfName}.pdf`);
    });
  }

  render() {
    const { location } = this.props;

    if (location.state === undefined) {
      return (
        <AlertMessage
          variant="warning"
          message={
            "You can't access this page directly from the url. You need"
            + ' to get access from the orders history page.'
          }
        />
      );
    }

    const allDetails = location.state.products.map((item, index) => (
      <tr key={item.id}>
        <td>{location.state.date}</td>
        <td>{item.name}</td>
        <td>{location.state.quantities[parseInt(index, 10)]}</td>
        <td>{location.state.seller}</td>
        <td>{`${item.price} CUB`}</td>
      </tr>
    ));

    return (
      <Container>
        <h1>Order details</h1>
        <br />
        <div id="divToPrint">
          <Table striped bordered hover>
            <thead>
              <tr key="0">
                <th>Date</th>
                <th>Product name</th>
                <th>Quantity</th>
                <th>Seller</th>
                <th>Unit price</th>
              </tr>
            </thead>
            <tbody>{allDetails}</tbody>
          </Table>
          <p style={{ textAlign: 'right', marginRight: '2%' }}>
            <b>Total: </b>
            {' '}
            {location.state.total}
            {' '}
CUB
          </p>
        </div>

        <div style={{ textAlign: 'center', padding: '0.5em' }}>
          <Button
            type="button"
            variant="dark"
            size="lg"
            onClick={this.printDocument}
            style={{ marginRight: '2%' }}
          >
            Download Receipt
          </Button>
          <Link to="/profile/ordersHistory">
            <Button variant="dark" size="lg">
              Back to all orders
            </Button>
          </Link>
        </div>
      </Container>
    );
  }
}

OrderDetails.propTypes = {
  // eslint-disable-next-line
  location: PropTypes.object.isRequired
};

export default OrderDetails;
